<template>
  <view class="container">
    <!-- TabÊ†è -->
    <view class="tab-bar">
      <view 
        class="tab-item" 
        :class="{ active: currentTab === 'all' }"
        @click="switchTab('all')"
      >
        <view class="tab-content">
          <text class="tab-text">ÂÖ®ÈÉ®</text>
          <text class="tab-count">({{ statistics.total }})</text>
        </view>
      </view>
      <view 
        class="tab-item" 
        :class="{ active: currentTab === 'available' }"
        @click="switchTab('available')"
      >
        <view class="tab-content">
          <text class="tab-text">ÂèØÁßüÁî®</text>
          <text class="tab-count">({{ statistics.available }})</text>
        </view>
      </view>
      <view 
        class="tab-item" 
        :class="{ active: currentTab === 'rented' }"
        @click="switchTab('rented')"
      >
        <view class="tab-content">
          <text class="tab-text">Â∑≤ÁßüÁî®</text>
          <text class="tab-count">({{ statistics.rented }})</text>
        </view>
      </view>
    </view>
    
    <!-- Êìç‰ΩúÊ†è -->
    <view class="action-bar">
      <view class="search-box">
        <input 
          class="search-input" 
          v-model="searchKeyword" 
          placeholder="ÊêúÁ¥¢ÊàøÈó¥Âè∑..."
          @input="onSearch"
        />
      </view>
      <view class="add-btn" @click="addRoom">
        <text class="add-icon">+</text>
        <text>Ê∑ªÂä†</text>
      </view>
    </view>

    <!-- ÊàøÈó¥ÂàóË°® -->
    <scroll-view class="room-list" scroll-y="true" @scrolltolower="loadMore">
      <view class="room-item" v-for="room in roomList" :key="room._id" @click="viewRoom(room)">
        <view class="room-header">
          <view class="room-number">{{ room.room_number }}Âè∑Êàø</view>
          <view class="header-actions">
            <button class="header-btn tenant" @click.stop="createRental(room)" v-if="room.status === 'available'">
              Âá∫Áßü
            </button>
            <button class="header-btn renewal" @click.stop="renewRental(room)" v-if="room.status === 'rented'">
              Áª≠Áßü
            </button>
            <button class="header-btn terminate" @click.stop="terminateRental(room)" v-if="room.status === 'rented'">
              ÈÄÄÁßü
            </button>
          </view>
        </view>
        
        
        <view class="tenant-info-card" v-if="room.current_tenant">
          <view class="tenant-main-info">
            <view class="tenant-name-phone">
              <text class="tenant-name">{{ room.current_tenant.name }}</text>
              <text class="tenant-phone" @tap="callTenant(room.current_tenant.phone)">
                üìû {{ room.current_tenant.phone }}
              </text>
            </view>
            <view class="tenant-status">
              <text class="status-badge">Âú®Áßü</text>
            </view>
          </view>
          <view class="rental-period" v-if="room.current_rental">
            <text class="period-text">üìÖ {{ formatDateRange(room.current_rental.rent_start_date, room.current_rental.rent_end_date) }}</text>
            <view class="days-remaining" :class="getDaysRemainingClass(room.current_rental.rent_end_date)">
              <text class="days-text">{{ getDaysRemaining(room.current_rental.rent_end_date) }}</text>
            </view>
          </view>
        </view>
      </view>
      
      <!-- Âä†ËΩΩÊõ¥Â§ö -->
      <view class="load-more" v-if="hasMore">
        <text>{{ loading ? 'Âä†ËΩΩ‰∏≠...' : '‰∏äÊãâÂä†ËΩΩÊõ¥Â§ö' }}</text>
      </view>
    </scroll-view>

  </view>
</template>

<script>
export default {
  data() {
    return {
      roomList: [],
      loading: false,
      hasMore: true,
      pageNum: 1,
      pageSize: 10,
      currentTab: 'all', // ÂΩìÂâçÈÄâ‰∏≠ÁöÑTab
      searchKeyword: '', // ÊêúÁ¥¢ÂÖ≥ÈîÆËØç
      searchTimer: null, // ÊêúÁ¥¢Èò≤ÊäñÂÆöÊó∂Âô®
      statistics: {
        total: 0,
        rented: 0,
        available: 0
      }
    }
  },
  
  onLoad() {
    this.loadRooms();
    this.loadStatistics();
  },
  
  onPullDownRefresh() {
    this.refreshData();
  },
  
  onShow() {
    // È°µÈù¢ÊòæÁ§∫Êó∂Âà∑Êñ∞Êï∞ÊçÆÔºåÁ°Æ‰øù‰ªéÂÖ∂‰ªñÈ°µÈù¢ËøîÂõûÊó∂Êï∞ÊçÆÊòØÊúÄÊñ∞ÁöÑ
    this.refreshData();
  },
  
  methods: {
    // Âä†ËΩΩÊàøÈó¥ÂàóË°®
    async loadRooms(isRefresh = false) {
      if (this.loading) return;
      
      this.loading = true;
      
      if (isRefresh) {
        this.pageNum = 1;
        this.roomList = [];
        this.hasMore = true;
      }
      
      try {
        // ÊûÑÂª∫ËØ∑Ê±ÇÊï∞ÊçÆ
        const requestData = {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          searchKeyword: this.searchKeyword
        };
        
        // Âè™Êúâ‰∏çÊòØ"ÂÖ®ÈÉ®"Êó∂Êâç‰º†ÈÄístatusÂèÇÊï∞
        if (this.currentTab !== 'all') {
          requestData.status = this.currentTab;
        }
        
        const result = await uniCloud.callFunction({
          name: 'room-management',
          data: {
            action: 'getRooms',
            data: requestData
          }
        });
        
        if (result.result.code === 0) {
          const { list, total } = result.result.data;
          
          if (isRefresh) {
            this.roomList = list;
          } else {
            this.roomList = [...this.roomList, ...list];
          }
          
          this.hasMore = this.roomList.length < total;
          this.pageNum++;
        } else {
          uni.showToast({
            title: result.result.message,
            icon: 'none'
          });
        }
      } catch (error) {
        console.error('Âä†ËΩΩÊàøÈó¥ÂàóË°®Â§±Ë¥•:', error);
        uni.showToast({
          title: 'Âä†ËΩΩÂ§±Ë¥•',
          icon: 'none'
        });
      } finally {
        this.loading = false;
        uni.stopPullDownRefresh();
      }
    },
    
    // Âä†ËΩΩÁªüËÆ°‰ø°ÊÅØ
    async loadStatistics() {
      try {
        const promises = [
          this.getRoomCount(''),
          this.getRoomCount('rented'),
          this.getRoomCount('available')
        ];
        
        const results = await Promise.all(promises);
        
        this.statistics = {
          total: results[0],
          rented: results[1],
          available: results[2]
        };
      } catch (error) {
        console.error('Âä†ËΩΩÁªüËÆ°‰ø°ÊÅØÂ§±Ë¥•:', error);
      }
    },
    
    // Ëé∑ÂèñÊàøÈó¥Êï∞Èáè
    async getRoomCount(status) {
      const requestData = {
        pageSize: 1, // Âè™ÈúÄË¶ÅËé∑ÂèñÊÄªÊï∞Ôºå‰∏çÈúÄË¶ÅÂÖ∑‰ΩìÊï∞ÊçÆ
        pageNum: 1
      };
      
      // Âè™Êúâstatus‰∏ç‰∏∫Á©∫Êó∂Êâç‰º†ÈÄístatusÂèÇÊï∞
      if (status) {
        requestData.status = status;
      }
      
      const result = await uniCloud.callFunction({
        name: 'room-management',
        data: {
          action: 'getRooms',
          data: requestData
        }
      });
      
      return result.result.code === 0 ? result.result.data.total : 0;
    },
    
    
    // ÊêúÁ¥¢
    onSearch(e) {
      this.searchKeyword = e.detail.value;
      // Èò≤ÊäñÂ§ÑÁêÜ
      clearTimeout(this.searchTimer);
      this.searchTimer = setTimeout(() => {
        this.refreshData();
      }, 500);
    },
    
    // Âà∑Êñ∞Êï∞ÊçÆ
    refreshData() {
      this.loadRooms(true);
      this.loadStatistics();
    },
    
    // Âä†ËΩΩÊõ¥Â§ö
    loadMore() {
      if (this.hasMore && !this.loading) {
        this.loadRooms();
      }
    },
    
    // TabÂàáÊç¢
    switchTab(tab) {
      if (this.currentTab === tab) return;
      this.currentTab = tab;
      this.refreshData();
    },
    
    // Êü•ÁúãÊàøÈó¥ËØ¶ÊÉÖ
    viewRoom(room) {
      uni.navigateTo({
        url: `/pages/room-detail/room-detail?id=${room._id}`
      });
    },
    
    
    // Ê∑ªÂä†ÊàøÈó¥
    addRoom() {
      uni.navigateTo({
        url: '/pages/room-edit/room-edit'
      });
    },
    
    // ÂàõÂª∫ÁßüËµÅÂÖ≥Á≥ª
    createRental(room) {
      uni.navigateTo({
        url: `/pages/tenant-info/tenant-info?roomId=${room._id}&action=create`
      });
    },
    
    
    // ÁªàÊ≠¢ÁßüËµÅÂÖ≥Á≥ª
    terminateRental(room) {
      if (!room.current_rental_id) return;
      
      uni.showModal({
        title: 'Á°ÆËÆ§ÈÄÄÁßü',
        content: `Á°ÆÂÆöË¶ÅÁªàÊ≠¢${room.room_number}Âè∑ÊàøÁöÑÁßüËµÅÂÖ≥Á≥ªÂêóÔºü`,
        success: async (res) => {
          if (res.confirm) {
            try {
              uni.showLoading({ title: 'Â§ÑÁêÜ‰∏≠...' });
              
              const result = await uniCloud.callFunction({
                name: 'room-management',
                data: {
                  action: 'terminateRental',
                  data: {
                    rental_id: room.current_rental_id,
                    termination_reason: 'Ê≠£Â∏∏ÈÄÄÁßü'
                  }
                }
              });
              
              if (result.result.code === 0) {
                uni.showToast({
                  title: 'ÈÄÄÁßüÊàêÂäü',
                  icon: 'success'
                });
                this.refreshData();
              } else {
                uni.showToast({
                  title: result.result.message,
                  icon: 'none'
                });
              }
            } catch (error) {
              console.error('ÈÄÄÁßüÂ§±Ë¥•:', error);
              uni.showToast({
                title: 'Êìç‰ΩúÂ§±Ë¥•',
                icon: 'none'
              });
            } finally {
              uni.hideLoading();
            }
          }
        }
      });
    },
    
    
    // Ëé∑ÂèñÁä∂ÊÄÅÊñáÊú¨
    getStatusText(status) {
      const statusMap = {
        available: 'ÂèØÁßüÁî®',
        rented: 'Â∑≤ÁßüÁî®',
        maintenance: 'Áª¥‰øÆ‰∏≠'
      };
      return statusMap[status] || 'Êú™Áü•';
    },
    
    // Ê†ºÂºèÂåñÊó•ÊúüËåÉÂõ¥
    formatDateRange(startDate, endDate) {
      if (!startDate || !endDate) return '--';
      
      const start = new Date(startDate);
      const end = new Date(endDate);
      
      const formatDate = (date) => {
        const year = date.getFullYear();
        const month = String(date.getMonth() + 1).padStart(2, '0');
        const day = String(date.getDate()).padStart(2, '0');
        return `${year}-${month}-${day}`;
      };
      
      return `${formatDate(start)} Ëá≥ ${formatDate(end)}`;
    },
    
    // ËÆ°ÁÆóÂâ©‰ΩôÂ§©Êï∞
    getDaysRemaining(endDate) {
      if (!endDate) return '';
      
      const end = new Date(endDate);
      const now = new Date();
      const diffTime = end - now;
      const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
      
      if (diffDays < 0) {
        return 'Â∑≤ËøáÊúü';
      } else if (diffDays === 0) {
        return '‰ªäÊó•Âà∞Êúü';
      } else if (diffDays <= 7) {
        return `${diffDays}Â§©ÂêéÂà∞Êúü`;
      } else if (diffDays <= 30) {
        return `${diffDays}Â§©ÂêéÂà∞Êúü`;
      } else {
        return `${Math.floor(diffDays / 30)}‰∏™ÊúàÂêéÂà∞Êúü`;
      }
    },
    
    // Ëé∑ÂèñÂâ©‰ΩôÂ§©Êï∞Ê†∑ÂºèÁ±ª
    getDaysRemainingClass(endDate) {
      if (!endDate) return '';
      
      const end = new Date(endDate);
      const now = new Date();
      const diffTime = end - now;
      const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
      
      if (diffDays < 0) {
        return 'expired';
      } else if (diffDays <= 7) {
        return 'urgent';
      } else if (diffDays <= 30) {
        return 'warning';
      } else {
        return 'normal';
      }
    },
    
    // Êã®ÊâìÁîµËØù
    callTenant(phoneNumber) {
      uni.makePhoneCall({
        phoneNumber: phoneNumber,
        fail: (err) => {
          console.error('Êã®ÊâìÁîµËØùÂ§±Ë¥•:', err);
          uni.showToast({
            title: 'Êã®ÊâìÂ§±Ë¥•',
            icon: 'none'
          });
        }
      });
    },
    
    // Áª≠Áßü
    renewRental(room) {
      if (!room.current_rental_id) return;
      
      uni.navigateTo({
        url: `/pages/rental-renewal/rental-renewal?rentalId=${room.current_rental_id}&roomId=${room._id}`
      });
    }
  }
}
</script>

<style>
.container {
  background-color: #f5f5f5;
  min-height: 100vh;
  /* #ifdef H5 */
  padding-bottom: 70px;
  /* #endif */
}

/* TabÊ†èÊ†∑Âºè */
.tab-bar {
  display: flex;
  background-color: #fff;
  border-bottom: 1rpx solid #eee;
}

.tab-item {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 24rpx 12rpx;
  position: relative;
  min-width: 0; /* Èò≤Ê≠¢ÊñáÂ≠óÊ∫¢Âá∫ */
}

.tab-content {
  display: flex;
  align-items: center;
  gap: 8rpx;
}

.tab-item.active {
  color: #007AFF;
}

.tab-item.active::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 60rpx;
  height: 4rpx;
  background-color: #007AFF;
  border-radius: 2rpx;
}

.tab-text {
  font-size: 32rpx;
  font-weight: bold;
}

.tab-count {
  font-size: 26rpx;
  color: #999;
  opacity: 0.8;
}

.tab-item.active .tab-count {
  color: #007AFF;
}


/* Êìç‰ΩúÊ†èÊ†∑Âºè */
.action-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20rpx;
  background-color: #fff;
  border-bottom: 1rpx solid #eee;
}

.search-box {
  flex: 1;
  margin-right: 20rpx;
}

.search-input {
  width: 100%;
  padding: 16rpx 20rpx;
  background-color: #f8f9fa;
  border-radius: 20rpx;
  font-size: 28rpx;
  border: none;
}

.add-btn {
  display: flex;
  align-items: center;
  padding: 16rpx 24rpx;
  background-color: #007AFF;
  color: white;
  border-radius: 20rpx;
  font-size: 28rpx;
}

.add-icon {
  font-size: 32rpx;
  margin-right: 8rpx;
}

.room-list {
  flex: 1;
  padding: 20rpx 0;
}

.room-list .room-item {
  margin:10rpx 0;
}

.room-item {
  background-color: #fff;
  border-radius: 16rpx;
  padding: 24rpx;
  box-shadow: 0 6rpx 20rpx rgba(0,0,0,0.08);
  border: 1rpx solid #f0f0f0;
  transition: all 0.3s ease;
  width: 100%;
  box-sizing: border-box;
}

.room-item:active {
  transform: translateY(2rpx);
  box-shadow: 0 4rpx 12rpx rgba(0,0,0,0.1);
}

.room-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16rpx;
  padding-bottom: 16rpx;
  border-bottom: 1rpx solid #f5f5f5;
}

.room-number {
  font-size: 40rpx;
  font-weight: bold;
  color: #1a1a1a;
  display: flex;
  align-items: center;
}

.room-number::before {
  content: 'üè†';
  font-size: 32rpx;
  margin-right: 12rpx;
}

.header-actions {
  display: flex;
  gap: 8rpx;
  align-items: center;
}

.header-btn {
  padding: 8rpx 16rpx;
  border-radius: 16rpx;
  font-size: 22rpx;
  font-weight: 500;
  border: none;
  color: white;
  min-width: 60rpx;
  text-align: center;
}


.header-btn.tenant {
  background: linear-gradient(135deg, #722ed1 0%, #9254de 100%);
}

.header-btn.renewal {
  background: linear-gradient(135deg, #13c2c2 0%, #36cfc9 100%);
}

.header-btn.terminate {
  background: linear-gradient(135deg, #ff4d4f 0%, #ff7875 100%);
}


/* ÁßüÊà∑‰ø°ÊÅØÂç°Áâá */
.tenant-info-card {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 12rpx;
  padding: 20rpx;
  margin-top: 12rpx;
  color: white;
  box-shadow: 0 4rpx 12rpx rgba(102, 126, 234, 0.3);
}

.tenant-main-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12rpx;
}

.tenant-name-phone {
  display: flex;
  align-items: center;
  flex: 1;
}

.tenant-name {
  font-size: 30rpx;
  font-weight: bold;
  color: #fff;
  margin-right: 16rpx;
}

.tenant-phone {
  font-size: 26rpx;
  color: rgba(255, 255, 255, 0.9);
  text-decoration: underline;
  cursor: pointer;
}

.tenant-status {
  flex-shrink: 0;
}

.status-badge {
  background-color: rgba(82, 196, 26, 0.2);
  color: #52c41a;
  padding: 6rpx 12rpx;
  border-radius: 16rpx;
  font-size: 22rpx;
  font-weight: bold;
  border: 1rpx solid rgba(82, 196, 26, 0.3);
}

.rental-period {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.period-text {
  font-size: 24rpx;
  color: rgba(255, 255, 255, 0.9);
  flex: 1;
}

.days-remaining {
  display: flex;
  justify-content: center;
  padding: 8rpx 16rpx;
  border-radius: 16rpx;
  margin-top: 8rpx;
}

.days-remaining.normal {
  background-color: rgba(82, 196, 26, 0.2);
  border: 1rpx solid rgba(82, 196, 26, 0.3);
}

.days-remaining.warning {
  background-color: rgba(250, 173, 20, 0.2);
  border: 1rpx solid rgba(250, 173, 20, 0.3);
}

.days-remaining.urgent {
  background-color: rgba(255, 77, 79, 0.2);
  border: 1rpx solid rgba(255, 77, 79, 0.3);
}

.days-remaining.expired {
  background-color: rgba(140, 140, 140, 0.2);
  border: 1rpx solid rgba(140, 140, 140, 0.3);
}

.days-text {
  font-size: 24rpx;
  font-weight: bold;
  color: rgba(255, 255, 255, 0.95);
}



.load-more {
  text-align: center;
  padding: 40rpx;
  color: #999;
  font-size: 28rpx;
}

</style>